    color: #424a5e;
  }

  .xz_search_input .search_result_con {
    position: absolute;
    left: 15px;
    top: 45px;
    z-index: 200;
  }

  .xz_search_input .search_result {
    min-width: 200px;
    max-width: 700px;
    min-height: 100px;
    max-height: 250px;
    overflow-y: auto;
    border: 1px solid #ddd;
    background: #fff;
  }

  .xz_search_input .search_result_con::before {
    content: '';
    display: block;
    width: 0;
    height: 0;
    position: absolute;
    left: 80px;
    top: -19px;
    border: 10px solid #fff;
    border-top: 10px solid transparent;
    border-left-color: transparent;
    border-right-color: transparent;
  }

  . .xz_total_domain {
    width: 100%;
    height: 400px;
    display: flex;
  }

  .xz_region {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    padding: 0 35px;
    box-sizing: border-box;
    background: #ffffff;
  }

  .xz_region .single_city {
    display: flex;
    float: left;
    align-items: center;
    width: 25%;
    cursor: pointer;
    font-size: 14px;
    color: #424a5e;
    line-height: 25px;
    padding: 5px 0;
  }

  .xz_region .single_city i {
    font-style: normal;
    padding: 3px 8px;
  }

  .xz_region .single_city:hover {
    color: #5f4b8b;
  }

  .xz_checkbox {
    position: relative;
    width: 15px;
    height: 15px;
    display: inline-block;
    margin-left: 5px;
  }

  .xz_custom_checkbox {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    background: #fff;
    border: 1px solid #ddd;
    box-sizing: border-box;
  }

  .xz_checkbox input[type="checkbox"] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    margin: 0;
    opacity: 0;
    z-index: 2;
    cursor: pointer;
  }

  .xz_checkbox input[type="checkbox"]:checked+.xz_custom_checkbox::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;

  }
</style>


